import { useEffect, useState } from "react";

// ユーザーの型を定義
interface User {
  id: number;
  username: string;
}

export default function Home() {
  // useState に型を指定
  const [users, setUsers] = useState<User[]>([]);

  useEffect(() => {
    fetch("./api/users")
      .then((res) => res.json()) // .json() として関数を呼び出す
      .then((data: User[]) => setUsers(data));
  }, []);

  return (
    <div className="container w-11/12 mx-auto p-8">
        <div className="headerContainer">
          <h1 className="font-bold text-5xl text-center">患者管理アプリ</h1>
        </div>

        <div className="mainContents">
          <ul>
            {users.map((user) => (
              <li key={user.id}>{user.username} </li>
            ))}
          </ul>
        </div>

    </div>
  );
}
